.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4.5rem;
  background-color: rgba(0, 0, 0, 0.20);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  /* height: 25rem; */
  /* z-index: 0; */
  /* overflow: hidden; */
}

.main-image img {
  width: 110%;
  height: 100vh;
  object-fit: cover;
  border: 1px solid red;
  /* transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem); */
}
.notification {
  background-color:#8a2b06;
  border-radius: 40%;
  padding: 3px;
}

@media screen and (max-width:600px) {
  .header{
    flex-direction: column;
  }
  
}