.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  /* margin-top: -10rem; */
  position: absolute;
  /* background-color: #383838; */
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  /* color: white; */
  color:black;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}



@media screen and (max-width:600px) {
  .summary {
    top: 60%;
    left: 50%;
  }
  
}
